(() => {
  const refs = {
    openModalBtn: document.querySelector('[data-shop-open]'),
    closeModalBtn: document.querySelector('[data-shop-close]'),
    modal: document.querySelector('[data-shop]'),
  };

  refs.openModalBtn.addEventListener('click', toggleModal);
  refs.closeModalBtn.addEventListener('click', toggleModal);

  function toggleModal() {
    refs.modal.classList.toggle('is-hidden');
  }
})();

(() => {
  const refs = {
    openthankBtn: document.querySelector('[data-thank-you-open]'),
    closethankBtn: document.querySelector('[data-thank-you-close]'),
    thank: document.querySelector('[data-thank-you]'),
  };

  refs.openthankBtn.addEventListener('click', togglethank);
  refs.closethankBtn.addEventListener('click', togglethank);

  function togglethank() {
    refs.thank.classList.toggle('is-hidden');
  }
})();

(() => {
  const mobileMenu1 = document.querySelector('.menu-box');
  const openMenuBtn1 = document.querySelector('.open-menu');
  const closeMenuBtn1 = document.querySelector('.nav-menu');

  const toggleMenu = () => {
    const isMenuOpen =
      openMenuBtn1.getAttribute('aria-expanded') === 'true' || false;
    openMenuBtn1.setAttribute('aria-expanded', !isMenuOpen);
    openMenuBtn1.classList.toggle('is-toggle');
    mobileMenu1.classList.toggle('is-open');
    const scrollLockMethod = !isMenuOpen
      ? 'disableBodyScroll'
      : 'enableBodyScroll';
    bodyScrollLock[scrollLockMethod](document.body);
  };

  openMenuBtn1.addEventListener('click', toggleMenu);
  closeMenuBtn1.addEventListener('click', toggleMenu);

  // Close the mobile menu on wider screens if the device orientation changes
  window.matchMedia('(min-width: 768px)').addEventListener('change', e => {
    if (!e.matches) return;
    openMenuBtn1.classList.remove('is-toggle');
    mobileMenu1.classList.remove('is-open');
    openMenuBtn1.setAttribute('aria-expanded', false);
    bodyScrollLock.enableBodyScroll(document.body);
  });
})();

document.addEventListener('DOMContentLoaded', function () {
  var images = document.querySelectorAll('.hero-fruit, .hero-box');

  images.forEach(function (image) {
    image.classList.add('slide-in');
  });
});

document.addEventListener('DOMContentLoaded', function () {
  // Adaugă clasa "show" pentru a aplica tranzitiile la încărcarea paginii
  document.querySelector('.hero-transition').classList.add('show');
  document.querySelector('.hero-left').classList.add('show');
  document.querySelector('.hero-right').classList.add('show');
});
